import React from 'react';
import './style.sass'
import Mask from "../../img/ilon-mask-leave-tesla.jpg";
import {Link} from 'react-scroll'
function LastPage(props) {
    const {} = props
    return (
        <div className='row p-0 m-0'>
            <div className='present-skills col-xl-12 text-center'>
                <h1 className='title-skill mt-3'>Thank you for looking at my resume</h1>
                <div className='line mx-auto mb-5 '>

                </div>
            </div>
            <div className='col-xl-12 text-white text-center '>
               <p className='m-5'>
                    Вы досмотрели мое резюме до конца. Я надеюсь я оставил о себе только хорошие впечатления. Буду рад положительному отклику. <br/> Спасибо!
               </p>
                <Link as={Link} to="PresentPage" spy={true}>
                    <button className='btn-restart'>
                        Back to the beginning
                    </button>
                </Link>
            </div>
        </div>
    );
}

export {LastPage};