import React from 'react';
import {ProgressBar} from "react-bootstrap";
import './style.sass'
function MySkills(props) {

    const skillList = [
        {name: 'HTML/CSS', progress: 90},
        {name: 'SASS', progress: 80},
        {name: 'JS', progress: 70},
        {name: 'React', progress: 80},
        {name: 'Redux', progress: 80},
        {name: 'Saga', progress: 80},
        {name: 'Ps', progress: 50},
        {name: 'Ai', progress: 70},
    ]

    const SkillItems = () => {
      return   skillList.map(el => (
          <div className='col-xl-3 col-sm-6 col-12 ' key={el.name}>
              <div className='content-skill p-5 m-2 text-center'>
                  <h5 className='name-skill'>{el.name}</h5>
                  <h2 className='mt-3 mb-3 text-white'>{el.progress} %</h2>
                  <ProgressBar now={el.progress} />
              </div>
          </div>
        ))
    }

    return (
        <div className='row p-0 m-0'>
            <div className='present-skills col-xl-12 text-center'>
                <p className='name-block mt-4'>SKILLS</p>
                <h1 className='title-skill mt-3'>My skills</h1>

                <p className='subtitle-skill mt-3 mb-3 '>
                   Перечень моих профессиональных навыков и умений. <br/> Самооценка высроена по уровню Junior
                </p>
                <div className='line mx-auto mb-5 '>

                </div>
            </div>
            <div className='skill-box mb-5   '>
                <div className='row p-0 m-0'>
                    <SkillItems />
                </div>
            </div>
        </div>
    );
}

export {MySkills};