import React from 'react';
import './style.sass'
import {Container, Nav, Navbar} from "react-bootstrap";
import {Link} from "react-scroll"

function Navigation(props) {
    const {} = props
    return (

                <Navbar className='navigation dark-blue' collapseOnSelect expand="lg" >
                    <Container>
                        <Navbar.Brand href="#home" className='logo'>Ruslan</Navbar.Brand>
                        <Navbar.Toggle aria-controls="responsive-navbar-nav"  className='color-white'/>
                        <Navbar.Collapse className="justify-content-end" id="responsive-navbar-nav">
                            <Nav className="justify-content-end text-white    " activeKey="/home">
                                <Nav.Item className='p-2'>
                                    <Nav.Link href="/home" as={Link} to="PresentPage" spy={true} className='navigation-link'>Present me</Nav.Link>
                                </Nav.Item>
                                <Nav.Item className='p-2'>
                                    <Nav.Link eventKey="link-1" as={Link} to="Skills" spy={true}  className='navigation-link'>Skills</Nav.Link>
                                </Nav.Item>
                                <Nav.Item className='p-2'>
                                    <Nav.Link eventKey="link-2" as={Link} to="AboutMe" spy={true} className='navigation-link'>About me</Nav.Link>
                                </Nav.Item>
                                <Nav.Item className='p-2'>
                                    <Nav.Link className='navigation-link'  as={Link} to="Task" spy={true}>
                                        Challenge
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item className='p-2'>
                                    <Nav.Link className='navigation-link'  as={Link} to="LastPage" spy={true}>
                                        Final
                                    </Nav.Link>
                                </Nav.Item>
                            </Nav>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>


    );
}

export {Navigation};
