import React from 'react';
import Mask from '../../img/ilon-mask-leave-tesla.jpg'
import './style.sass'

function Task(props) {
    const {} = props
    return (
        <div className='row p-0 m-0'>
            <div className='present-skills col-xl-12 text-center'>
                <p className='name-block mt-5 '>CHALLENGE</p>
                <h1 className='title-skill mt-5 '>The logic challenge from Elon Musk</h1>

                <div className='line mx-auto mb-5 mt-5 '>

                </div>
            </div>
            <div className="col-xl-6 text-white ">
                <p>
                    Представьте, что вы стоите на поверхности Земли.
                    Вы проходите одну милю на юг, одну на запад и одну на север и оказываетесь в той же точке, откуда начали движение.
                </p>
                <p><span className='span-q'>Вопрос:</span>Где вы находитесь?</p>
            </div>
            <div className="col-xl-6">
                <img src={Mask} alt="Mask" className='img-fluid img-mask mb-4 '/>
            </div>
        </div>
    );
}

export {Task};