import React from 'react';
import './style.sass'

import Ruslan from '../../img/svet.jpg'
function MyPresentPage(props) {
    const {} = props


    const social = [
        {soc: 'Vkontakte', href: 'https://vk.com/id369766753'},
        {soc: 'Instagram', href: 'https://instagram.com/aaezzakmii?utm_medium=copy_link'},
    ]


    const SocialList = () => {
        return social.map(el => (
            <li key={el.soc} className='social-li p-2  '>
                <a href={el.href}>{el.soc}</a>
            </li>
        ))
    }

    return (
        <div className='row justify-content-center align-items-center '>
            <div className="col-xl-6 mt-5 mb-5 ">

                <ul className='text-white m-0 p-0 '>
                   <SocialList />
                    <li className='social-li p-2  '>
                        <i>ruslan_tokko@mail.ru</i>
                    </li>
                </ul>

                <h1 className='present-me pt-3 pb-3'>
                    I am Ruslan Tokko
                </h1>
                <p className='subtitle-me'>
                    Мне 20 лет и я являюсю студентом Волгоградского государственного университета. Обучаюсь на специальности "Реклама и связи с общественнотью", паралельно работаю и увлекаюсь программированием.
                    Я бы хотел попробовать себя в роли тестировщика в компании "Яндекс"
                </p>
            </div>
            <div className="col-xl-6 mb-5 ">
                <img src={Ruslan} alt="Avatar" className='d-block mx-auto ' style={{height: '550px', borderRadius: '10px'}}/>
            </div>
        </div>
    );
}

export {MyPresentPage};